    flex-direction: column;
    justify-content: flex-start;
    margin-bottom: 15px;
  }

  &__logo {
    margin-bottom: 26px;
  }

  &__logo-icon {
    display: block;
    width: 100%;
    height: auto;
  }

  &__title {
    font-weight: bold;
    font-size: 20px;
    line-height: 23px;

    color: #FFFFFF;
    margin-bottom: 20px;
  }

  &__undertitle {
    font-size: 16px;
    line-height: 24px;
    text-align: center;
    color: #534A59;
    margin-bottom: 10px;
  }

