  &__checkbox {
    display: flex;
    align-items: center;
    justify-content: flex-start;
    font-size: 15px;
    color: #4D5878;
    margin-bottom: 27px;

    &-span {
      color: #534A59;
    }

    .link {
      text-decoration: underline;
      color: #FFD788;
    }

    &-input {
      -webkit-appearance: none;
      -moz-appearance: none;
      -ms-appearance: none;
      -o-appearance: none;
      appearance: none;
      position: relative;
      top: 0;
      right: 0;
      bottom: 0;
      left: 0;
      height: 20px;
      width: 20px;
      transition: all 0.15s ease-out 0s;
      background: #161B29;
      border: none;
      color: #fff;
      cursor: pointer;
      display: inline-block;
      margin-right: 15px;
      outline: none;
      z-index: 1000;
      border-radius: 3px;
      font-size: 15px;

      &:hover {
        background: #313448;
      }

      &:checked {
        background: #161B29;
      }

      &:checked:before {
        height: 12px;
        width: 12px;
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
        content: '';
        display: block;
        background-color: #FFD788;
        //font-size: 26.66667px;
        border-radius: 3px;
        //text-align: center;
        //line-height: 20px;
      }

      &:checked:after {
        -webkit-animation: click-wave 0.65s;
        -moz-animation: click-wave 0.65s;
        animation: click-wave 0.65s;
        background: #FFD788;
        content: '';
        display: block;
        position: relative;
        z-index: 100;
        border-radius: 50%;
      }
    }


    @keyframes click-wave {
      0% {
        height: 20px;
        width: 20px;
        opacity: 0.35;
        position: relative;
      }
      100% {
        height: 100px;
        width: 100px;
        margin-left: -40px;
        margin-top: -40px;
        opacity: 0;
      }
    }
  }

  &__button {
    border-radius: 3px;
    border: 0;
    background: #FFD788;
    width: 100%;
    max-width: 282px;
    height: 48px;
    line-height: 48px;
    text-transform: uppercase;
    font-size: 14px;
    text-align: center;
    color: #1B1711;
    font-weight: 700;
    margin-bottom: 25px;

    &:hover {
      opacity: 0.7;
    }
  }

  &__textarea {
    width: 100%;
    min-height: 155px;
    -webkit-appearance: none;
    background-color: #161B29;
    box-sizing: border-box;
    //border-radius: 0;
    border-radius: 4px;
    //border: 0;
    padding: 17px 13px 14px;
    border: 1px solid transparent;
    //padding: 0 13px;
    //line-height: 48px;
    font-size: 14px;
    color: #FFFFFF;
    transition: all 0.3s ease;
    margin-bottom: 25px;

    &::placeholder {
      color: #515C7A;
    }
  }

}