.about {
    position: relative;
    margin-bottom: 72px;

    @include xs-down {
        overflow-x: hidden;
        padding: 70px 0;
    }
    
    @include md-down {
        margin-bottom: 20px;
    }
    
    &__wrap {
        display: flex;
        align-items: center;
        margin: 0 auto;
    }
    
    &__wrap-content {
        display: flex;
        align-items: flex-start;
        margin: 0 auto;
        
        @include xs-down {
            margin: 0;
        }
    }
    
    &__img-wrap {
        position: relative;
        z-index: 1;
        margin-right: 200px;
        
        @include md-down {
            margin-right: 45px;
        }
    
        @include xs-down {
            position: absolute;
            top: 0;
            left: 60%;
        }
        
        img {
            
            @include xs-down {
                display: block;
                height: 502px;
            }
            
        }
    }
    
    &__info-wrap {
        width: 100%;
        max-width: 330px;
        padding-bottom: 56px;
        margin-top: 20%;
        
        @include xs-down {
            max-width: 180px;
            margin-top: 0;
        }
    }
    
    &__title-wrap {
        display: flex;
        align-items: center;
        margin-bottom: 32px;
        
        @include xs-down {
            flex-direction: column-reverse;
            align-items: flex-start;
        }
    }
    
    &__title {
        margin-right: 40px;
    }
    
    &__unisex {
        font-weight: 500;
        font-size: 14px;
        line-height: 23px;
        color: #FFD788;
        
        @include xs-down {
            margin-bottom: 5px;
        }
        
        &-img {
            margin-right: 10px;
            vertical-align: sub;
        }
    }
    
    &__text-wrap {
    
    }
    
    &__text {
        font-weight: 300;
        font-size: 16px;
        line-height: 26px;
        margin-bottom: 22px;
        
        &--first {
            font-weight: 500;
            
            img {
                vertical-align: text-bottom;
                margin-right: 6px;
            }
            
            span {
            
            }
        }

        &--hidden {
            //display: block;
            transition: max-height 0.2s ease-out 0s;
            overflow: hidden;
            //max-height: 100%;
            //height: 100%;
            ////visibility: visible;
            ////opacity: 1;
        }
    }
    
    &__btn {
        display: flex;
        align-items: center;
        padding: 7px 14px;
        background: #171419;
        border: 1px solid rgba(34, 30, 37, 0.52);
        border-radius: 6px;
        color: #ffffff;
        outline: none;
        
        &-text {
            margin-right: 10px;
            
           
        }
        
        &-img {
            &--close {
        
            }
    
            &--open {
                display: none;
            }
        }
    }
    
    &__background {
        --wdth: 47vw;
        content: '';
        position: absolute;
        left: 0;
        top: -25%;
        width: var(--wdth);
        height: calc(var(--wdth) * 1.42);
        background-image: url("../img/main/shape1.svg");
        background-size: cover;
        background-position: center;
        background-repeat: no-repeat;
        z-index: 0;
        
        @include sm-down {
            display: none;
        }
    }
}