.graph {
    margin-bottom: 150px;
    &__wrap {
        display: flex;
        align-items: flex-start;
        justify-content: space-around;
        
        @include md-down {
            align-items: center;
            flex-direction: column;
        }
    }
    
    &__left {
        @include md-down {
            margin-bottom: 120px;
        }
    }
    
    &__right {
    
    }
    
    &__title {
        font-weight: 900;
        font-size: 30px;
        line-height: 40px;
        color: #FFFFFF;
        margin-bottom: 28px;
    
        @include md-down {
            text-align: center;
        }
    }
    
    &__content {
        display: flex;
        align-items: flex-start;
        
        @include sm-down {
            flex-direction: column;
            align-items: center;
        }
    }
    
    &__chart-wrap {
        margin-right: 44px;
    
        @include sm-down {
            margin-right: 0;
            margin-bottom: 43px;
        }
    }
    
    &__chart {
    
    }
    
    &__chart {
        
        img {
        
        }
    }
    
    &__list-wrap {
    
    }
    
    &__list {
    
    }
    
    &__item {
        display: flex;
        align-items: center;
    }
    
    &__item-percent {
        width: 30px;
        color: rgba(207, 207, 221, 0.4);
        margin-right: 14px;
    }
    
    &__item-label {
        font-size: 14px;
        line-height: 27px;
        color: #CFCFDD;
    }
    
    &__item-color {
        width: 12px;
        height: 12px;
        border-radius: 2px;
        margin-right: 17px;
        
        &.ieo1 {
            background-color: #AB59B8;
        }
        
        &.ieo2 {
            background-color: #EE4F44;
        }
    
        &.ieo3 {
            background-color: #B5EB28;
        }
    
        &.team {
            background-color: #6611F2;
        }
    
        &.partner {
            background-color: #5DD0F9;
        }
    
        &.bounti {
            background-color: #EA9A56;
        }
    
        &.community {
            background-color: #1275F9;
        }
    
        &.dev {
            background-color: #21B7AE;
        }
    
        &.market {
            background-color: #FF8D41;
        }
    
        &.legal {
            background-color: #A9A9A9;
        }
    
        &.overhead {
            background-color: #FFC42F;
        }
    
        &.reserve {
            background-color: #3475FB;
        }
    }
}
