html {
    font-size: 16px;
    box-sizing: border-box;
    -ms-overflow-style: scrollbar;
    //scroll-behavior: smooth;
}

body {
    font-size: 100%;
    font-weight: 400;
    line-height: 1.15;
    font-family: $FontPrimary;
    color: $color_primary;
    background-color: #0B080E;
    position: relative;
    min-width: 320px;
    overflow-x: hidden;
    -ms-overflow-style: scrollbar;
    -ms-text-size-adjust: 100%;
    -webkit-text-size-adjust: 100%;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    scroll-behavior: smooth;
}

.container {
    width: 100%;
    max-width: 1140px;
    padding-right: 15px;
    padding-left: 15px;
    margin-right: auto;
    margin-left: auto;
    
    &--inner {
        max-width: 1470px;
        width: 100%;
        padding-right: 15px;
        padding-left: 15px;
        margin-right: auto;
        margin-left: auto;
    }
}

.container-fluid {
    width: 100%;
    padding-right: 15px;
    padding-left: 15px;
    margin-right: auto;
    margin-left: auto;
}

.d-hide {
    visibility: hidden;
}

.d-none {
    display: none !important;
}


h1,
h2,
h3,
h4,
h5,
h6,
ul {
    margin: 0;
    padding: 0;
}

.main {

}

h1, h2 {
    color: #ffffff;
    font-weight: 900;
    font-size: 50px;
    line-height: 53px;
}

h3 {
    font-weight: 900;
    font-size: 30px;
    line-height: 40px;
    color: #ffffff;
}

h4 {
    font-size: 22px;
    line-height: 32px;
    color: #ffffff;
    font-weight: 400;
}

.orange {
    color: #FFD788;
}

.gray {
    color: rgba(207, 207, 221, .4);
}

.white {
    color: #ffffff;
}

.overflow {
    overflow: hidden;
    //padding-right: 17px;
}