.preloader {
  display: none;
  position: fixed;
  width: 100%;
  height: 100%;
  //display: flex;
  align-items: center;
  justify-content: center;
  background-color: #0A080E;
  z-index: 999999;

  &.is-active {
    display: flex;
  }

  &__wrap {
    display: flex;
    align-items: center;
    flex-direction: column;
    justify-content: center;
  }

  &__logo {
    position: relative;
    margin-bottom: 30px;
    width: 64px;
    height: 64px;
  }

  &__logo-inner {
    position: absolute;
    display: flex;
    align-items: center;
    justify-content: center;
    top: 50%;
    left: 50%;
    width: 27px;
    height: 27px;
    transform: translate(-50%, -50%);
    z-index: 2;
  }

  &__img {

    &--first {
      display: block;
      //position: absolute;
      //top: 0;
      //left: 50%;
      //transform: translateY(-50%);
      z-index: 1;
    }

    &--second {
      display: block;
      animation: preload-inner 2s infinite ease-in-out;
      z-index: 2;
      transition: all 0.5s ease;
    }
  }
}

@keyframes preload-inner {
  0% {
    transform: rotate(0) scale(1);
  }

  33% {
    transform: rotate(360deg) scale(1);
  }

  66% {
    transform: scale(2.4);
  }

  100% {
    transform: rotate(-720deg) scale(2.4);
  }
}