.our-team {
    background-image: url("../img/main/shape2.svg");
    background-size: cover;
    background-position: center;
    background-repeat: no-repeat;
    padding-top: 162px;
    padding-bottom: 172px;

    .container {
        width:100%;
        max-width:100%;
        padding-right: 0;
        padding-left: 0;
    }

    @include sm-down {
        padding-top: 80px;
        padding-bottom: 100px;
    }
    
    &__wrap {
    
    }
    
    &__title {
        text-align: center;
        margin-bottom: 40px;
    }
    
    &__slider-wrap {
        position: relative;
    }
    
    &__slider {
        display: flex;
        align-items: center;
        justify-content: space-between;
    }
    
    &__slider-block {
        min-width: 200px;
        opacity: 0.5;
    }
    
    &__slider-avatar {
        //margin: 0 auto 9px;
        text-align: center;
    }
    
    &__avatar-img {
        width: 86px;
        height: 86px;
        display: block;
        border-radius: 50%;
        margin: 0 auto 9px;
    }
    
    &__slider-title {
        font-weight: bold;
        line-height: 23px;
        text-align: center;
        color: #FFFFFF;
        margin-bottom: 4px;
        font-size: 18px;
    }
    
    &__slider-undertitle {
        font-weight: 300;
        line-height: 23px;
        text-align: center;
        color: #CFCFDD;
        margin-bottom: 18px;
        font-size: 14px;
    }
    
    &__slider-social {
        display: flex;
        align-items: center;
        justify-content: center;
    }
    
    &__slider-social-link {
        display: block;
        width: 24px;
        height: 24px;
        margin-right: 20px;
        
        &:last-child {
            margin-right: 0;
        }
        
        img {
        
        }
    }
    
    &__slider-control {
        position: absolute;
        bottom: 10px;
        left: 50%;
        transform: translateX(-50%);
        display: flex;
        align-items: center;
        justify-content: center;
        z-index: 100;
    }
    
    &__slider-btn {
        margin-right: 205px;
        cursor: pointer;
        
        &:last-child {
            margin-right: 0;
        }
        
        &--left {
            position: absolute;
            display: block;
            right: -100px;
        }
        
        &--right {
            display: block;
            position: absolute;
            left: 100px;
            transform: rotate(180deg);
        }
    }
}


.swiper-slide-active {
    z-index: 1;
    opacity: 1;
    transition: all 0.3s ease;
    
    
    .our-team__slider-avatar {
        img {
            width: 168px;
            height: 168px;
            //display: block;
            border-radius: 50%;
        }
    }
    
    .our-team__slider-title {
        font-weight: bold;
        font-size: 22px;
        line-height: 23px;
        text-align: center;
        color: #FFFFFF;
        margin-bottom: 4px;
    }
    
    .our-team__slider-undertitle {
        font-weight: 300;
        font-size: 16px;
        line-height: 23px;
        text-align: center;
        color: #CFCFDD;
        margin-bottom: 18px;
    }
    
    .our-team__slider-avatar {
        margin: 0 auto 18px;
    }
}

.swiper-container {

}
.swiper-slide {

}

.swiper-slide-active {
    min-width: 200px !important;
}
.swiper-slide-next {
    min-width: 180px !important;
}