.intro {
    width: 100%;
    max-width: 450px;
    
    @include sm-down {
        max-width: 290px;
    }
    
    &__wrap {
    
    }
    
    &__header-wrap {
        margin-top: 50px;
        margin-bottom: 34px;
        text-align: center;
        
        @include sm-down {
            margin-bottom: 46px;
        }
    }
    
    &__title {
        margin-bottom: 13px;
        font-size: 50px;
        line-height: 53px;
        
        @include sm-down {
            font-size: 30px;
            line-height: 37px;
        }
    }
    
    &__undertitle {
        font-size: 22px;
        line-height: 32px;
        
        @include sm-down {
            font-size: 18px;
            line-height: 32px;
        }
    }
    
    &__btn-wrap {
        display: flex;
        align-items: center;
        flex-wrap: wrap;
        justify-content: center;
        
        @include sm-down {
            flex-direction: column-reverse;
        }
    }
    
    &__btn {
        margin-right: 19px;
        margin-bottom: 32px;
        
        @include sm-down {
            margin-right: 0;
            margin-bottom: 20px;
        }
        
        &:last-child {
            margin-right: 0;
        }
    }
}