.main-nav {
    padding: 50px 0 0 0;
    display: flex;
    align-items: center;
    justify-content: space-between;
    
    @include sm-down {
        padding: 24px 0 0 0;
    }
    
    &__main-logo {
        margin-right: 80px;
        
        @include sm-down {
            margin-right: 0;
        }
    }
    
    &__list {
        display: flex;
        align-items: center;
        justify-content: flex-start;
    
        @include md-down {
            display: none;
        }
    }
    
    &__item {
        margin-right: 70px;
    }
    
    &__item-link {
        color: rgba(255, 255, 255, .4);
        font-size: 16px;
        line-height: 24px;
        text-decoration: none;
        transition: all 0.2s ease;
        
        &:hover {
            color: rgba(255, 255, 255, .6);
        }
    }
    
    &__leftside {
        display: flex;
        align-items: center;
        justify-content: flex-start;
        z-index: 999;
    }
    
    &__rightside {
        z-index: 999;

        &--btc {
            display: none !important;
        }

        @include sm-down {
            display: none;
        }
    }

    &__rightside-account {

    }
    
    &__rightside-list {
        display: flex;
        align-items: center;
        justify-content: flex-start;


    }
    
    &__rightside-item {
        margin-right: 50px;
        
        &:last-child {
            margin-right: 0;
        }
    }
    
    &__rightside-link {
        display: flex;
        align-items: center;
        position: relative;
        color: rgba(255, 255, 255, 1);
        font-size: 16px;
        line-height: 24px;
        text-decoration: none;
        transition: all 0.2s ease;

        &--coin {

            img {
                margin-right: 8px;
            }

            span {
                font-size: 14px;
                line-height: 24px;
            }
        }
    
        &:hover {
            color: rgba(255, 255, 255, .6);
            opacity: .6;
        }
    }

    &__link-badge {
        position: absolute;
        top: -9px;
        left: 7px;
    }

    &__account {

    }
}