.account {
  position: relative;
  display: flex;
  align-items: center;
  justify-content: space-between;
  transition: all 0.3s ease;

  &__avatar {
    //width: 280px;
    //height: 77px;
    //padding-left: 20px;
    //padding-right: 20px;
    //width: 100%;
    display: flex;
    align-items: center;
    justify-content: space-between;
    cursor: pointer;

    &:hover {
      //background-color: #172136;
    }
  }

  &__avatar-wrap {
    display: flex;
    align-items: center;
    justify-content: flex-start;
  }

  &__avatar-photo {
    width: 38px;
    height: 38px;
    margin-right: 15px;
  }

  &__avatar-img {
    display: block;
    width: 100%;
    border-radius: 50%;
    border: 2px solid rgba(255, 255, 255, 0.4);
  }

  &__avatar-name {
    font-weight: 500;
    font-size: 14px;
    line-height: 24px;
    color: #FFFFFF;
  }

  &__avatar-angle {
    transition: all 0.3s ease;
    margin-bottom: 0.1em;
    margin-left: 11px;

    &--show {
      transform: rotate(180deg);
    }

    img {
      display: block;
    }

  }

  &__menu {
    //display: none;
    opacity: 0;
    width: 150px;
    //height: calc(100vh - 77px);
    position: absolute;
    top: 50px;
    left: 0;
    z-index: 11;
    background-color: #171419;
    transition: all 0.3s ease;
    pointer-events: none;
    border-radius: 5px;

    &:before {
      content: '';
      position: absolute;
      left: 12%;
      top: -12px;
      width: 0;
      height: 0;
      border-style: solid;
      border-width: 0 13px 13px 13px;
      transform: translateX(-50%);
      border-color: transparent transparent #171419 transparent;
      z-index: 999;
    }

    &--show {
      transition: all 0.3s ease;
      opacity: 1;
      pointer-events: auto;
      //display: block;
    }

    &--show {
      //display: block;
    }
  }

  &__menu-list {

  }

  &__menu-item {

    transition: all 0.3s ease;
    &:hover {
      //background-color: #342d38;

      .account__menu-link {
        color: rgba(255, 255, 255, 0.6)
      }
    }
    &--logout {
      border-top: 1px solid #342d38;
    }
  }

  &__menu-link {
    display: flex;
    align-items: center;
    justify-content: flex-start;
    padding: 18px 9px;
    text-decoration: none;
    //display: block;
    color: rgba(255, 255, 255, 0.4);

    img {
      margin-right: 10px;
    }
  }

}