.header-mobile {
    box-sizing: border-box;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    position: fixed;
    top: 0;
    right: 0;
    z-index: 99999;
    margin-top: 108px;
    min-height: calc(100vh - 108px);
    width: 100%;
    background-color: #000;
    padding: 20px 20px 40px;
    transform: translateX(100%);
    transition: .3s ease-in-out;
    overflow-y: auto;
    text-align: left;
}

.header-mobile__btn-wrap {
    margin-top: 35px;
}

.header-mobile__btn {
    padding: 15px 0;
    letter-spacing: 3px;
    text-align: left;
    padding-left: 24px;
}

.header-mobile.opened {
    transform: translateX(0);
}

.header-mobile__burger-open {
    display: none;
}

.header-mobile__nav-menu {
    .item {

    }

    .item--btn {
        //padding-top: 30px;
    }
}

.header-mobile__nav-menu .link {
    display: block;
    width: 100%;
    font-size: 16px;
    line-height: 36px;
    text-decoration: none;
    padding-top: 10px;
    padding-bottom: 10px;
    color: #ffffff;
    transition: all .3s;
    text-transform: uppercase;
    text-align: left;
    padding-left: 24px;
    letter-spacing: 3px;
}

.header-mobile__nav-menu .link:active,.header-mobile__nav-menu .link:hover {
    color: #fff;
}

.header-mobile__social {
    margin: 0 auto;
    padding: 20px;
}
