.roadmap {
    padding-top: 80px;
    margin-bottom: 125px;
    overflow-x: hidden;
    
    @include md-down {
        padding-top: 40px;
        margin-bottom: 60px;
    }
    
    .container {
        max-width: 1200px;
    }
    
    &__title {
        text-align: center;
    
        @include md-down {
            //text-align: left;
            padding-left: 20px;
        }
        
        @include sm-down {
            //text-align: center;
            padding-left: 0;
        }
    }
    
    &__wrap {
        position: relative;
        display: flex;
        align-items: flex-start;
        justify-content: space-between;
        padding-top: 70px;
        
        &:after {
            content: '';
            position: absolute;
            left: -160%;
            top: 154px;
            width: 100vw;
            height: 1px;
            background-color: rgba(38, 30, 43, .99);
        }
        
        &:before {
            content: '';
            position: absolute;
            right: -160%;
            top: 154px;
            width: 100vw;
            height: 1px;
            background-color: #FFD788;
        }
        
        @include lg-down {
            padding-left: 10px;
            padding-right: 10px;
            
            &:after {
                display: none;
            }
    
            &:before {
                display: none;
            }
        }
    
        @include md-down {
            //flex-direction: column;
            flex-wrap: wrap;
            justify-content: center;
            //padding-left: 50px;
            padding-top: 20px;
        }
    }
    
    &__block {
        width: 100%;
    
        @include md-down {
            width: 250px;
            margin-bottom: 50px;
            margin-top: 50px;
            padding-left: 50px;
        }
    
        @include sm-down {
            margin-bottom: 30px;
            margin-top: 30px;
        }
        
        &--done {
            color: #332C3D;
    
            .roadmap__list {
                margin-left: -11px;
            }
        }
        
        &--now {
            color: #FFD788;
        }
        
        &--next {
            color: #786A83;
        }
    }
    
    &__block-title {
        position: relative;
        color: #332C3D;
        padding-bottom: 22px;
        padding-left: 15px;
    
        @include md-down {
            padding-bottom: 0;
        }
        
        h3 {
            font-weight: bold;
            font-size: 30px;
            line-height: 26px;
            color: #332C3D;
        }
    
        &--done {
            color: #332C3D;
            border-bottom: 1px solid rgba(38, 30, 43, .99);
            
            h3 {
                color: #332C3D;
            }
            
            .roadmap__block-year {
                color: #332C3D;
            }
    
            @include md-down {
                border: 0;
        
                &:before {
                    display: none;
                }
            }
        }
    
        &--now {
            color: #FFD788;
            border-bottom: 1px solid #FFD788;
            
            h3 {
                color: #FFD788;
            }
    
            .roadmap__block-year {
                color: #FFD788;
            }
    
            &:before {
                content: url("../img/sprites/intermediate-svg/roadmap-triangle.svg");
                position: absolute;
                left: -1px;
                bottom: -10px;
            }
    
            @include md-down {
                border: 0;
        
                &:before {
                    display: none;
                }
            }
        }
    
        &--next {
            color: #ffffff;
            border-bottom: 1px solid #FFD788;
            
            h3 {
                color: #ffffff;
            }
    
            .roadmap__block-year {
                color: #ffffff;
            }
    
            &:before {
                content: url("../img/sprites/intermediate-svg/roadmap-triangle.svg");
                position: absolute;
                left: -1px;
                bottom: -10px;
            }
    
            @include md-down {
                border: 0;
    
                &:before {
                    display: none;
                }
            }
        }
        
        
    }
    
    &__block-year {
        font-size: 16px;
        line-height: 36px;
        color: #332C3D;
    }
    
    &__list {
        padding-top: 27px;
        padding-right: 50px;
        
        @include lg-down {
            padding-right: 20px;
        }
    }
    
    &__item {
        display: flex;
        align-items: center;
        font-size: 16px;
        line-height: 23px;
        max-width: 140px;
        margin-bottom: 20px;
        
        img {
            margin-right: 10px;
        }
        
        span {
            display: block;
        }
    }
}