.btn {
    display: block;
    text-decoration: none;
    padding: 15px 20px;
    font-weight: 900;
    font-size: 16px;
    line-height: 24px;
    color: #1B1711;
    background-color: #FFD788;
    box-shadow: 0px 10px 10px rgba(0, 0, 0, 0.1);
    border-radius: 6px;
    text-transform: uppercase;
    
    &__icon {
        margin-right: 10px;
    }
    
    &--simple {
        background-color: transparent;
        font-weight: 900;
        font-size: 16px;
        line-height: 24px;
        color: #FFD788;
    }
}