.footer {
    padding-top: 20px;
    padding-bottom: 60px;
    &__wrap {
        display: flex;
        align-items: flex-start;
        justify-content: space-between;
        
        @include sm-down {
            flex-direction: column-reverse;
            align-items: center;
        }
    }
    
    &__left {
        display: flex;
        align-items: flex-start;
        justify-content: space-between;
    
        @include sm-down {
            flex-direction: column-reverse;
            align-items: center;
        }
    }
    
    &__right {
        display: flex;
        align-items: flex-start;
        justify-content: space-between;
    }
    
    &__logo {
        margin-right: 110px;
        
        @include md-down {
            margin-right: 40px;
        }
    
        @include sm-down {
            margin-right: 0;
        }
    }
    
    &__logo-copy {
        font-size: 14px;
        line-height: 27px;
        color: rgba(207, 207, 221, 0.4);
        padding-left: 44px;
    }
    
    &__nav {
        display: flex;
        margin-right: 60px;
        
        @include md-down {
            margin-right: 25px;
        }
    
        @include sm-down {
            margin-right: 0;
            margin-bottom: 50px;
            width: 250px;
            justify-content: space-around;
        }
    }
    
    &__nav-list {
        display: flex;
        flex-direction: column;
        align-items: flex-start;
    }
    
    &__nav-item {
        margin-bottom: 12px;
        min-width: 135px;
    
        @include sm-down {
           min-width: auto;
        }
    }
    
    &__nav-link {
        font-weight: 500;
        font-size: 12px;
        line-height: 24px;
        text-transform: uppercase;
        text-decoration: none;
        color: rgba(255, 255, 255, 0.4);
        transition: all 0.2s ease-in-out;
    
        &:hover {
            opacity: 0.7;
        }
    }
    
    &__social {
        display: flex;
        flex-wrap: wrap;
        align-items: flex-start;
        width: 100%;
        max-width: 390px;
        //height: 100px;
        margin-right: 100px;
        
        @include lg-down {
            margin-right: 10px;
        }
        
        @include md-down {
            margin-right: 0;
        }
        
        @include sm-down {
            width: 250px;
            justify-content: flex-start;
            margin-bottom: 20px;
        }
    }
    
    &__social-link {
        display: block;
        //width: 20px;
        height: 20px;
        margin-right: 65px;
        margin-bottom: 32px;
        transition: all 0.3s ease-in-out;
    
        @include md-down {
            margin-right: 50px;
        }
        
        @include sm-down {
            margin-right: 0;
            flex-basis: 25%;
            //width: auto;
            text-align: center;
        }
        
        &:hover {
            opacity: 0.7;
        }
    }
    
    &__social-icon {
    
    }
    
    &__totop {
        
        @include md-down {
            display: none;
        }
     
        &-btn {
            background: #171419;
            border: 1px solid #221E25;
            box-sizing: border-box;
            border-radius: 6px;
            font-weight: bold;
            font-size: 12px;
            line-height: 24px;
            color: #FFD788;
            text-decoration: none;
            display: flex;
            align-items: center;
            padding: 2px 8px 2px 13px;
            min-width: 83px;
            
            span {
                font-size: 12px;
                line-height: 24px;
            }
            
            img {
                vertical-align: central;
                margin-left: 6px;
            }
        }
    }
}