.how {
    position: relative;
    z-index: 1;
    margin-bottom: 80px;
    padding-top: 72px;
    &__wrap {
    
    }
    
    &__title {
        margin-bottom: 60px;
        
        @include sm-down {
            text-align: center;
            margin-bottom: 45px;
        }
    }
    
    &__wrap-content {
        display: flex;
        align-items: center;
        justify-content: center;
        margin: 0 auto;
        
        @include sm-down {
            flex-direction: column;
        }
    }
    
    &__left {
        margin-right: 155px;
        
        @include md-down {
            margin-right: 75px;
        }
    
        @include sm-down {
            margin-right: 0;
        }
    }
    
    
    &__right {
        margin-top: -59px;
    
        @include md-down {
            margin-top: -96px;
            //margin-bottom: 60px;
        }
    
        @include sm-down {
            margin-top: 0;
        }
    }
    
    &__main-list {
        //padding-top: 30px;
    }
    
    &__main-item {
        display: flex;
        align-items: center;
        margin-bottom: 100px;
        
        @include sm-down {
            flex-direction: column;
            margin-bottom: 50px;
        }
    }
    
    &__main-img {
        margin-right: 41px;
    
        @include sm-down {
            margin-right: 0;
            margin-bottom: 30px;
        }
    }
    
    &__main-text {
        width: 100%;
        max-width: 280px;
        font-size: 16px;
        line-height: 26px;
        
        @include md-down {
            max-width: 220px;
        }
    
        @include sm-down {
            max-width: 280px;
        }
    }
    
    &__tooltip {
        position: relative;
        width: 260px;
        margin: 0 auto 20px;
        border-radius: 10px;
        background-color: #130F17;
        padding: 20px 30px;
        
        &:after {
            content: '';
            position: absolute;
            left: 50%;
            bottom: -19px;
            width: 0;
            height: 0;
            border-style: solid;
            border-width: 20px 13px 0 13px;
            transform: translateX(-50%);
            border-color: #130f17 transparent transparent transparent;
            z-index: 999;
        }
    }
    
    &__platform-list {
        border-radius: 16px;
        border: 3px solid rgba(38, 30, 43, .35);
        //padding: 30px;
    }
    
    &__platform-item {
        display: flex;
        align-items: center;
        //margin-bottom: 55px;
        padding: 28px;
        border-bottom: 1px solid rgba(38, 30, 43, .35);
    }
    
    &__platform-img {
        margin-right: 24px;
    }
    
    &__platform-text {
        width: 100%;
        max-width: 200px;
        font-size: 16px;
        line-height: 23px;
    }
}